import * as React from "react";
import {HTMLProps, PureComponent, StatelessComponent} from "react";
import {uniq} from "underscore";
import * as numeral from "numeral";
import {OnChangeField} from "./app";

numeral.register('locale', 'nl', {
    delimiters: {
        thousands: '.',
        decimal: ','
    },
    abbreviations: {
        thousand: 'k',
        million: 'm',
        billion: 'b',
        trillion: 't'
    },
    ordinal: function (number) {
        return number === 1 ? 'ste' : 'de';
    },
    currency: {
        symbol: '€'
    }
});
numeral.locale('nl');
numeral.defaultFormat('$0,0.00');

const WHITESPACE = /\s+/g;
const REGEX_NON_NUMBER = /[^0-9,.]+/g;
const REGEX_DUTCH_NUMBER = /^[0-9]*(\.[0-9]{3})*(,[0-9]+)?$/;

function normalizeTagName(name: string): string {
    return name.replace(WHITESPACE, "").toLowerCase();
}

export interface InputFieldProps {
    tag: string;
    i: string;
    onChangeListener: OnChangeField;
    extraClassNames?: string[];
    inactive?: boolean;
    val?: string;
}

export interface KostenProps extends InputFieldProps {
    tags: any;
    appliedCostsCategory: string;
}

export interface KostenState {
}

type KostenCalculator = (hoofdsom: number) => Numeral;

export interface CategoryIncassokosten {
    title: string;
    description: string;
    calculate: KostenCalculator;
}

function createIncassokostenCategory(title: string, description: string, calculate: KostenCalculator): CategoryIncassokosten {
    return {title, description, calculate};
}

/* @formatter:off */
export const categoryIncassokosten = {
    UNDER_2500:   createIncassokostenCategory("Tot en met €2500,-",  "15% over de hoofdsom (minimaal €40)",        (hoofdsom)     => numeral(Math.max(0.15 * hoofdsom, 40))),
    UNDER_5000:   createIncassokostenCategory("Tot en met €5000,-",  "€375 + 10% over (hoofdsom - €2.500)",        (hoofdsom)     => numeral(375 + (0.10 * (hoofdsom - 2500)))),
    UNDER_10000:  createIncassokostenCategory("Tot en met €10000,-", "€ 625 + 5% over (hoofdsom - €5.000)",        (hoofdsom)     => numeral(625 + (0.05 * (hoofdsom - 5000)))),
    UNDER_200000: createIncassokostenCategory("Tot en met €200000,-","€ 875 + 1% over (hoofdsom - € 10.000)",      (hoofdsom)     => numeral(875 + (0.01 * (hoofdsom - 10000)))),
    ABOVE_200000: createIncassokostenCategory("Boven €200000,-" ,    "€ 2.775 + 0,5% over (hoofdsom - € 200.000)", (hoofdsom      => (numeral(hoofdsom).subtract(200000).multiply(0.005)).add(2775))),
};
/* @formatter:on */

export type CATEGORY_INCASSOKOSTEN = keyof typeof categoryIncassokosten;

export function getCategoryIncassokosten(hoofdsom: number): CategoryIncassokosten {
    if (hoofdsom <= 2500) return categoryIncassokosten.UNDER_2500;
    else if (hoofdsom <= 5000) return categoryIncassokosten.UNDER_5000;
    else if (hoofdsom <= 10000) return categoryIncassokosten.UNDER_10000;
    else if (hoofdsom <= 200000) return categoryIncassokosten.UNDER_200000;
    else return categoryIncassokosten.ABOVE_200000;
}

const findCategory = function (key: keyof typeof categoryIncassokosten): CategoryIncassokosten {
    return categoryIncassokosten[key] as CategoryIncassokosten;
};

function parseNumber(value: string) {
    value = value.replace(REGEX_NON_NUMBER, "");
    if (value.match(REGEX_DUTCH_NUMBER)) {
        value = value
            .replace(/\./g, "")
            .replace(/,/g, ".");
    }
    const parsed = parseFloat(value.replace(/,/g, ""));
    if (!isNaN(parsed)) {
        return parsed;
    } else if (value.trim() === "")
        return 0;
    else return undefined;
}

export const BuitengerechtelijkeIncassokosten: StatelessComponent<KostenProps> = (props) => {
    const {tag, tags, i, onChangeListener, appliedCostsCategory} = props;
    const incassoKosten = tags[tag];
    return <div key={i + "-wrapper"} className="complex-prop table-row-group">
        <InputField {...props}
                    onChangeListener={(k: string, v: string) => {
                        // this.onHoofdsomChange(v, `tag-${i}`);
                        const hoofdsom = parseNumber(v);
                        if (!!hoofdsom) {
                            const category: CategoryIncassokosten = getCategoryIncassokosten(hoofdsom);
                            onChangeListener(tag, category.calculate(hoofdsom).format(), category.title);
                        }
                    }}
                    tag="hoofdsom"
                    i={`${i}-hoofdsom`} key={`${i}-hoofdsom`}
                    extraClassNames={["sub-prop"]}/>
        <div className="tr template-tag">
            <div className="td prop-label"/>
            <div className="td">
                <dl className="prop-explanation">
                    {Object.keys(categoryIncassokosten)
                        .map((key: keyof typeof categoryIncassokosten) => {
                            const cat: CategoryIncassokosten = findCategory(key);
                            const isApplied = cat.title === appliedCostsCategory;
                            return <div key={key}>
                                <dt className={isApplied ? "applied-category" : ""}>{cat.title}</dt>
                                <dd className={isApplied ? "applied-category" : ""}>{cat.description}</dd>
                            </div>;
                        })}
                </dl>
            </div>
        </div>
        <InputField {...props}
                    inactive={true}
                    val={typeof incassoKosten === "string" ? incassoKosten : categoryIncassokosten.UNDER_2500.calculate(0).format()}
                    extraClassNames={["result-prop"]}/>
    </div>;
};

export const InputField: StatelessComponent<InputFieldProps> = ({
                                                                    tag,
                                                                    i,
                                                                    onChangeListener,
                                                                    extraClassNames,
                                                                    inactive,
                                                                    val
                                                                }) => {
        const classNames = ((!!extraClassNames) ? extraClassNames : []).concat(["tr template-tag mdc-form-field"]);
        const inputProps: HTMLProps<HTMLInputElement> = {
            key: "input-" + i,
            onChange: (e: any) => onChangeListener(tag, e.target.value),
            disabled: inactive,
            type: "text",
            className: "mdc-text-field__input",
            id: `tag-${i}`,
            placeholder: tag
        };
        if (typeof val === "string") inputProps.value = val;

        return <div className={classNames.join(" ")}>
            <label className="td prop-label" htmlFor={`tag-${i}`}>{tag}</label>
            <div className="td mdc-text-field">
                {React.createElement("input", inputProps)}
                <div className="mdc-text-field__bottom-line"/>
            </div>
        </div>;
    }
;
export const InputFields: StatelessComponent<{
    tags?: any,
    onChangeListener: OnChangeField,
    appliedCostsCategory: string
}> = (props) =>
    <div
        className="table"
        id="template-fields">
        {!!props.tags ?

            uniq(Object.keys(props.tags)).map((tag, i: number) => {
                    const normalizedTagName = normalizeTagName(tag);
                    switch (normalizedTagName) {
                        case "buitengerechtelijkeincassokosten":
                            return <BuitengerechtelijkeIncassokosten
                                onChangeListener={props.onChangeListener}
                                appliedCostsCategory={props.appliedCostsCategory}
                                tag={tag as string}
                                tags={props.tags}
                                i={i + ""}
                                key={i + "-BuitengerechtelijkeIncassokosten"}/>;
                        default:
                            return <InputField key={i}
                                               onChangeListener={props.onChangeListener}
                                               tag={tag as string} i={i + ""}/>;
                    }
                }
            )

            : ""}
    </div>;



